import React, { useState } from "react";
import HeaderFile from "~/components/HeaderFile";
import { getDirectusClient } from "~/lib/directus";
import { Form, useLoaderData, useNavigate } from "@remix-run/react";
import { directusLoaderLink } from "~/utils/constants.js";
import { toast } from "react-toastify";
import DeleteModel from "~/components/deleteModel";
import Footer from "~/components/Footer";
import { sessionStorage } from "~/services/session.server";
import { authLogout, isLoggedIn } from "~/utils/db.server";
import { ISS_DENMARK_HQ } from "~/utils/constants";
import Select from "react-select";
import { useHydrated } from "remix-utils";

export const action = async ({ request }) => {
  await authLogout(request);
};

export const loader = async ({ request }) => {
  const url = new URL(request.url);
  const id = url.searchParams.get("id");
  await isLoggedIn(request);
  const session = await sessionStorage.getSession(
    request.headers.get("Cookie")
  );
  var sessionkey = session.get("sessionKey");
  const directus = await getDirectusClient(sessionkey);
  const current = await directus.users.me.read({ fields: ["*.*"] });
  const response = await directus.items("Rooms").readByQuery({
    fields: ["*.*.*"],
    filter: { id: { _eq: id } },
  });
  return {
    uId: id,
    data: response?.data,
    location_id: current?.selected_locations?.id,
  };
};

export function meta() {
  return { title: "Edit Rooms" };
}

export default function RoomsEdit() {
  const { data, uId, location_id } = useLoaderData();
  const [modalIsOpen, setIsOpen] = useState(false);
  const [nameData, setNameData] = useState(data[0]?.name);
  const [preventDouble, setPreventDouble] = useState(false);
  const navigate = useNavigate();
  const isHydrated = useHydrated();

  const directionData = [
    { name: "North", id: 1, direction: "Up" },
    { name: "South", id: 2, direction: "Down" },
    { name: "West", id: 3, direction: "Left" },
    { name: "East", id: 4, direction: "Right" },
  ];
  const [screenA, setScreenA] = useState(
    directionData?.find((e) => e?.direction == data[0]?.screen_a)
  );
  const [screenB, setScreenB] = useState(
    directionData?.find((e) => e?.direction == data[0]?.screen_b)
  );

  const updateHandle = async (e) => {
    if (nameData?.trim()?.length > 0) {
      if (screenA) {
        if (screenB) {
          setPreventDouble(true);
          const directus = directusLoaderLink;
          await directus
            .items("Rooms")
            .updateOne(uId, {
              name: nameData !== data[0]?.name ? nameData : data[0]?.name,
              screen_a: screenA?.direction,
              screen_b: screenB?.direction,
            })
            .then((res) => {
              navigate("/rooms/manage");
              toast.success("Updated successfully");
            })
            .catch((err) => {
              navigate(".", { replace: true });
              console.log(err);
              toast.error("Something went wrong");
            });
        } else {
          toast.error("Select Screen-B direction");
        }
      } else {
        toast.error("Select Screen-A direction");
      }
    } else {
      toast.error("Room name is required");
    }
  };

  const deleteHandle = async () => {
    const directus = directusLoaderLink;
    await directus
      .items("Rooms")
      .deleteOne(uId)
      .then((res) => {
        navigate("/rooms/manage");
        toast.success("Deleted successfully");
      })
      .catch((err) => {
        navigate(".", { replace: true });
        console.log(err);
        toast.error("Something went wrong");
      });
  };

  const RepeatSelection = (prop) => {
    const onChangeHandle = (e) => {
      prop === false ? setScreenA(e) : setScreenB(e);
      return "";
    };
    return (
      <div>
        {isHydrated ? (
          <Select
            id={"rooms"}
            instanceId={prop === false ? 1 : 2}
            classNamePrefix="cursor-pointer react-select"
            className="screen-direction react-select-container mt-1 block w-full rounded-md border border-gray-300 py-2 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
            maxMenuHeight={250}
            onChange={(e) => onChangeHandle(e)}
            isSearchable={false}
            components={{ IndicatorSeparator: () => null }}
            value={prop === false ? screenA : screenB}
            options={directionData}
            getOptionLabel={(option) => (
              <i
                className={`px-3 ri-arrow-${option?.direction
                  ?.trim()
                  ?.toLowerCase()}-line`}
              ></i>
            )}
            getOptionValue={(option) => option}
            styles={{
              control: (baseStyles) => ({
                ...baseStyles,
                border: "none",
                boxShadow: "none",
              }),
            }}
          />
        ) : null}
      </div>
    );
  };
  return (
    <div className="page-content">
      <HeaderFile
        title
        logout
        noLeftLogo={ISS_DENMARK_HQ?.includes(location_id) ? true : false}
      />
      <div className="main-content-area">
        <div className="page__wrap w-full flex justify-center items-center mh-145">
          <div className="border p-6 rounded-md border-gray-300 form-area e-allergens">
            <div className="max-w-[400px] w-full">
              <div className="flex justify-between items-center gap-5">
                <div className="flex justify-start items-center gap-3">
                  <div>
                    <button
                      type="button"
                      title="Backward"
                      onClick={() => navigate(-1)}
                      className="w-10 h-10 min-h-[40px] min-w-[40px] inline-flex items-center justify-center rounded-full border border-gray-100 bg-white  text-sm font-medium text-primary shadow-md hover:bg-gray-50 focus:outline-none focus:shadow-md sm:w-auto"
                    >
                      <span>
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          fill="none"
                          viewBox="0 0 24 24"
                          strokeWidth="3"
                          stroke="currentColor"
                          className="w-5 h-5"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            d="M15.75 19.5L8.25 12l7.5-7.5"
                          />
                        </svg>
                      </span>
                    </button>
                  </div>
                  <div>
                    <h1 className="text-xl font-semibold text-primary">
                      {data[0]?.name}
                    </h1>
                  </div>
                </div>
                <div className="flex justify-between items-center gap-5">
                  <button
                    type="button"
                    title="Delete Rooms"
                    onClick={(e) => setIsOpen(true)}
                    className="w-10 h-10 min-w-[40px] min-h-[40px] inline-flex items-center justify-center rounded-full border border-transparent bg-red-600 text-sm font-medium text-white shadow-sm hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-600 focus:ring-offset-2 sm:w-auto"
                  >
                    <span>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 24 24"
                        fill="none"
                        stroke="currentColor"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        className="feather feather-trash w-5 h-5"
                      >
                        <polyline points="3 6 5 6 21 6"></polyline>
                        <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
                      </svg>
                    </span>
                  </button>
                  <button
                    title="Update Rooms"
                    onClick={(e) => (preventDouble ? null : updateHandle(e))}
                    disabled={preventDouble}
                    className="w-10 h-10 min-w-[40px] min-h-[40px] inline-flex items-center justify-center rounded-full border border-transparent bg-primary text-sm font-medium text-white shadow-sm hover:bg-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2 sm:w-auto"
                  >
                    <span>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        fill="none"
                        viewBox="0 0 24 24"
                        strokeWidth="2"
                        stroke="currentColor"
                        className="w-5 h-5"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          d="M4.5 12.75l6 6 9-13.5"
                        />
                      </svg>
                    </span>
                  </button>
                </div>
              </div>
              <div className="mt-6 block max-w-[580px]">
                <Form action="#" method="POST">
                  <div className="space-y-6 bg-white py-6">
                    <div className="grid grid-cols-6 gap-6">
                      <div className="col-span-6">
                        <label
                          htmlFor="room-name"
                          className="block text-sm font-medium text-gray-700"
                        >
                          Room Name
                          <span className="text-red-600 text-base">*</span>
                        </label>
                        <input
                          type="text"
                          name="room-name"
                          id="room-name"
                          placeholder="Room Name"
                          required
                          value={nameData}
                          onChange={(e) => setNameData(e.target.value)}
                          autoComplete="given-name"
                          className="mt-1 block w-full rounded-md border border-gray-300 py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
                        />
                      </div>
                      <div className="col-span-6">
                        <label
                          htmlFor="direction-name"
                          className="block text-sm font-medium text-gray-700"
                        >
                          Screen A Direction
                          <span className="text-red-600 text-base">*</span>
                        </label>
                        {RepeatSelection(false)}
                      </div>
                      <div className="col-span-6">
                        <label
                          htmlFor="direction-name"
                          className="block text-sm font-medium text-gray-700"
                        >
                          Screen B Direction
                          <span className="text-red-600 text-base">*</span>
                        </label>
                        {RepeatSelection(true)}
                      </div>
                    </div>
                  </div>
                </Form>
              </div>
            </div>
          </div>
        </div>
      </div>
      {modalIsOpen ? (
        <DeleteModel open={setIsOpen} deleteHandle={deleteHandle} note={true} />
      ) : null}
      <Footer />
    </div>
  );
}
